@import url(https://fonts.googleapis.com/css2?family=Press+Start+2P&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.mainBody {
  padding: 0;
  margin: 0;
  overflow-y: hidden;
}

.mainBody {
    position: relative;
    margin: auto;
    padding: 0;
    height: calc(100vh - 50px);
    overflow-y: hidden;
}

.mainCenter {
    width: 90%;
    margin: auto;
}

.smartGrid {
    position: relative;
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(12rem, 1fr));
    text-align: center;
    overflow-y: scroll;
    height: calc(100vh - 200px);
}


.navbox {
    position: relative;
}

.navTitle {
    padding-top: 20px;
    padding-bottom: 20px;
    padding-left: 50px;
    font-size: 30px;
    font-weight: bold;
    color: yellow;
    font-family: 'Press Start 2P', cursive;
}

.navSpace{
    display: flex;
    justify-content: space-between;
}

.logButton{
    margin-top: 20px;
    margin-right: 20px;
}

.subTabs{
    font-family: 'Press Start 2P', cursive;
    color: white;
    font-size: 12px;
    margin-right: 25px;
}

.subTabs:hover {
    color: yellow;
    cursor: pointer;
    text-decoration: underline;
}

@media only screen and (max-width: 700px) {
    
    .navSpace{
        display: grid;
        text-align: center;
        justify-content: center;
    }

    .navTitle{
        margin: auto;
    }

    .logButton{
        margin: auto;
        padding-bottom: 10px;
    }
}


.searchBox {
    padding-top: 15px;
    width: 80%;
    position: relative;
    margin: auto;
    display: flex;
    padding-bottom: 15px;
}

.searchIcon {
    color: gray;
    padding-top: 20px;
    padding-right: 10px;
}

.searchField {
    width: 97%;
}

.onboardSmall{
    font-size: 12px;
}
.pokeCard{
    position: relative;
    text-align: center;
    background-color: rgba(128, 128, 128, 0.137);
    border-radius: 15px;
    margin: 10px;
    padding: auto;
    padding-top: 30px;
    padding-bottom: 30px;
    max-width: 300px;
    max-height: 300px;
    min-height: 160px;
}

.cardActive {
    background-color: rgba(128, 128, 128, 0.337);
    cursor: pointer;
}

.cardText {
    font-size: 12px;
    font-family: 'Press Start 2P', cursive;
    margin-top: 10px;
}

.favStar{
    position: absolute;
    right: 0;
    padding: 10px;
    top: 0;
    color: rgba(128, 128, 128, 0.281);
}

.favStar:hover {
    cursor: pointer;
    color: rgba(128, 128, 128, 0.481);
}

.active {
    color: blue;
    height: 20px;
}

.active:hover {
    color: blue;
}

.dialogBox{
    padding: 20px;
    text-align: center;
}

.boldText{
    font-weight: bold;
    font-size: 15px;
    padding-bottom: 5px;
}

.subText{
    font-size: 10px;
}

.underlineText {
    text-decoration: underline;
}
.onBoardBox{
    margin-top: -200px;
    width: 350px;
    margin-left: 50px;
}

.onboardLink:hover {
    cursor: pointer;
    color: blue;
    text-decoration: underline;
}

.onboardGrid {
    display: grid;
    width: 80%;
}

.onboardHeader {
    font-size: 22px;
    font-weight: bold;
    margin-bottom: 10px;
}

.onboardInput{
    margin-bottom: 6px;
}

.activeTab {
    color: rgb(252, 252, 0);
}
.entryBox{
    width: 100vw;
    height: 100vh;
    position: relative;
    display: grid;
    grid-template-columns: 3fr 2fr;
    overflow-x: hidden;
}

.split{ 
    margin: auto;
    width: 100%;
    height: 100%;
    display: grid;
    place-content: center;
    overflow: hidden;
    position: relative; 
}

.leftImage{
    position: absolute;
    height: 85%;
    width: 100%;
    top: 0;
    right: 0;
    object-fit: cover;
    object-position: center center;
}

@media only screen and (max-width: 850px) {
    
    .left{
        width: 0px;
    }

    .entryBox{
        display: flex;
    }
}

